"use client";

import React, { useState, useEffect, useCallback } from "react";
import { useAuth } from "@/hooks/useAuth";
import { useRouter } from "next/navigation";
import { ChevronLeftIcon, DollarLineIcon, EyeIcon } from "@/icons";
import { vendaService, type Venda } from "@/services/api/venda.service";
import CobrancaCard from "@/components/cliente/CobrancaCard";
import Pagination from "@/components/ui/Pagination";
import { ThemeToggleButton } from "@/components/common/ThemeToggleButton";
import toast from "react-hot-toast";

export default function CobrancasPage() {
  const { user } = useAuth();
  const router = useRouter();
  const [vendas, setVendas] = useState<Venda[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState("");
  const [statusFilter, setStatusFilter] = useState<string>("all");
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [totalItems, setTotalItems] = useState(0);
  const [itemsPerPage, setItemsPerPage] = useState(9);

  const loadVendas = useCallback(async () => {
    try {
      setLoading(true);
      
      const params = {
        page: currentPage,
        limit: itemsPerPage,
        search: searchTerm || undefined
      };

      const response = await vendaService.list(params);
      const data = response.data;
      console.log('Vendas:', data);
      setVendas(data);
      setTotalPages(response.pagination?.totalPages || 1);
      setTotalItems(response.pagination?.total || 0);
    } catch (error) {
      console.error('Erro ao carregar vendas:', error);
      toast.error('Erro ao carregar vendas');
    } finally {
      setLoading(false);
    }
  }, [currentPage, searchTerm, itemsPerPage]);

  useEffect(() => {
    loadVendas();
  }, [loadVendas]);

  const handleSearch = (e: React.FormEvent) => {
    e.preventDefault();
    setCurrentPage(1);
    loadVendas();
  };

  const handleViewDetails = (vendaId: number) => {
    router.push(`/dashboard/cliente/cobranca/${vendaId}`);
  };

  const filteredVendas = vendas.filter(venda => {
    if (statusFilter === "all") return true;
    return venda.status === statusFilter;
  });

  if (loading && vendas.length === 0) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="animate-spin rounded-full h-32 w-32 border-b-2 border-blue-600"></div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-4">
            <button
              onClick={() => router.back()}
              className="p-2 hover:bg-gray-100 dark:hover:bg-gray-700 rounded-lg transition-colors"
              aria-label="Voltar"
            >
              <ChevronLeftIcon className="w-5 h-5 text-gray-600 dark:text-gray-400" />
            </button>
            <div>
              <h1 className="text-2xl font-bold text-gray-900 dark:text-white">
                Minhas Cobran√ßas
              </h1>
              <p className="text-gray-600 dark:text-gray-400 mt-1">
                Visualize todas as suas vendas e cobran√ßas
              </p>
            </div>
          </div>
          <div className="flex items-center space-x-2">
            <ThemeToggleButton />
            <DollarLineIcon className="w-8 h-8 text-blue-600 dark:text-blue-400" />
          </div>
        </div>
      </div>

      {/* Filtros */}
      <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
        <div className="flex flex-col md:flex-row gap-4">
          {/* Busca */}
          <form onSubmit={handleSearch} className="flex-1">
            <div className="relative">
              <EyeIcon className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-gray-400" />
              <input
                type="text"
                placeholder="Buscar cobran√ßas..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="w-full pl-10 pr-4 py-2 border border-gray-300 dark:border-gray-600 rounded-lg bg-white dark:bg-gray-700 text-gray-900 dark:text-white focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              />
            </div>
          </form>

          {/* Filtro de Status */}
          <div className="flex items-center space-x-2">
            <select
              value={statusFilter}
              onChange={(e) => setStatusFilter(e.target.value)}
              className="px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-lg bg-white dark:bg-gray-700 text-gray-900 dark:text-white focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              aria-label="Filtrar por status"
            >
              <option value="all">Todos os Status</option>
              <option value="PENDENTE">Pendente</option>
              <option value="PAGO">Pago</option>
            </select>
          </div>
        </div>
      </div>

      {/* Controles de Pagina√ß√£o */}
      <div className="flex justify-between mb-4 gap-2 flex-wrap">
        <div className='flex items-center gap-2'>
          <label className='text-sm text-gray-600 dark:text-gray-400'>Quantidade por p√°gina</label>
          <input
            type="number"
            min="1"
            max="100"
            value={itemsPerPage}
            onChange={(e) => {
              const newValue = Number(e.target.value) || 9;
              setItemsPerPage(newValue);
              setCurrentPage(1);
            }}
            className="w-20 px-2 py-1 border border-gray-300 dark:border-gray-600 rounded bg-white dark:bg-gray-700 text-gray-900 dark:text-white text-sm"
            aria-label="Quantidade de itens por p√°gina"
          />
        </div>
        <Pagination
          currentPage={currentPage}
          totalPages={totalPages}
          onPageChange={setCurrentPage}
        />
      </div>

      {/* Lista de Cobran√ßas */}
      {loading ? (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {[1, 2, 3].map((n) => (
            <div key={n} className="animate-pulse bg-gray-100 dark:bg-gray-800 rounded-lg h-48" />
          ))}
        </div>
      ) : (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {filteredVendas.length > 0 ? (
            filteredVendas.map((venda) => (
              <CobrancaCard
                key={venda.id}
                venda={venda}
                onViewDetails={() => handleViewDetails(venda.id)}
              />
            ))
          ) : (
            <div className="col-span-full text-center py-12">
              <DollarLineIcon className="w-16 h-16 text-gray-400 mx-auto mb-4" />
              <h3 className="text-lg font-medium text-gray-900 dark:text-white mb-2">
                Nenhuma cobran√ßa encontrada
              </h3>
              <p className="text-gray-600 dark:text-gray-400">
                Voc√™ ainda n√£o possui cobran√ßas cadastradas.
              </p>
            </div>
          )}
        </div>
      )}

      {/* Pagina√ß√£o */}
      {totalPages > 1 && (
        <div className="mt-6">
          <div className="flex justify-end mb-4 gap-2 flex-wrap">
            <Pagination
              currentPage={currentPage}
              totalPages={totalPages}
              onPageChange={setCurrentPage}
            />
          </div>
        </div>
      )}
    </div>
  );
}
